import {Component, OnDestroy, OnInit} from '@angular/core';
import * as $ from 'jquery';
import {saveAs} from 'file-saver';
import {HttpService} from '../../service/http.service';

export class Secretary {
  id: string;
  subjectName: string;
  student: string;
  subjectType: string;
  guidanceScore: number;
  ratingScore: number;
  replyScore: number;
  state: string;

  constructor(id, subjectName, student, subjectType, guidanceScore, ratingScore, replyScore, state) {
    this.id = id;
    this.subjectName = subjectName;
    this.student = student;
    this.subjectType = subjectType;
    this.guidanceScore = guidanceScore;
    this.ratingScore = ratingScore;
    this.replyScore = replyScore;
    this.state = state;
  }
}

@Component({
  selector: 'app-secretary',
  templateUrl: './secretary.component.html',
  styleUrls: ['./secretary.component.scss']
})
export class SecretaryComponent implements OnInit, OnDestroy {
  secretaries: Array<Secretary> = [];
  flags: Array<string> = ['guidanceScore', 'ratingScore', 'replyScore'];
  timer: any;
  today: Date;
  texts: string;

  constructor(private http: HttpService) {
    this.timer = setInterval(() => {
      this.today = new Date();
    }, 50);
  }

  ngOnInit() {
    this.http.reply().subscribe((res: any) => {
      this.secretaries = res.extend.secretaries;
    }, (error: any) => {
      alert(error);
    });
  }

  check(data: any) {
    const total = Number(data);
    if (total < 60) {
      return '0';
    } else if (total >= 60 && total < 70) {
      return '1';
    } else if (total >= 70 && total < 80) {
      return '2';
    } else if (total >= 80 && total < 90) {
      return '3';
    }
  }

  // 修改表中数据
  edit(e: any) {
    const span = e.target;
    const input = e.target.nextSibling;
    // 根据表格文本创建文本框 并加入表表中--文本框的样式自己调整
    this.texts = span.innerText;
    input.value = this.texts;
    span.innerText = '';
    input.style.display = 'block';
    $(input).focus();
  }

  inputEdit(e: any, data: any, id: string) {
    const input = e.target;
    const span = e.target.parentNode.firstChild;
    const newText = input.value;
    const pattern = /^\d{1,3}$/;
    // 只能是1-3位数字，且数字范围必须是0-100
    if (!pattern.test(newText) || newText < 0 || newText > 100 || newText === '') {
      alert('数据非法');
      return;
    }
    if (confirm('确定要修改吗？')) {
      this.http.updateReply(id, data).subscribe((res: any) => {
        if (res === true) {
          // 移除文本框,显示新值
          e.target.style.display = 'none';
          span.innerText = newText;
        }
      }, (error: any) => {
        alert(error);
      });
    } else {
      // 移除文本框,不保存修改
      e.target.style.display = 'none';
      span.innerText = this.texts;
    }
  }

  exportTable(e: any) {
    const data = '<html><head><meta charset=\'utf-8\' /></head><body>'
      + $('#table')[0].outerHTML + '</body></html>';
    const blob = new Blob([data], {
      type: 'applicatio n/vnd.openxmlformats-officedocument.wordprocessingml.document'
    });
    saveAs(blob, '学生成绩表.doc');
  }

  // 页面销毁时，清除时间
  ngOnDestroy(): void {
    clearInterval(this.timer);
  }
}
